<template>
	<div class="loginPagelogin mt-2">
			<h1>
				登录
			</h1>
				<div style="width: 330px; background-color: white;">
					<div class="zptitle" >
						<span>淘多多不会以任何理由要求你转账汇款，谨防诈骗</span>
					</div>
					
					<div class="loginform">
							<el-form class="col">
							<el-form-item >
								<el-input v-model="loginform.account" placeholder="账号" ></el-input>
							</el-form-item>
							<el-form-item>
								<el-input v-model="loginform.password" placeholder="密码" ></el-input>
							</el-form-item>
							<div>
								<el-link style="font-size: 12px; float: right;" :underline="false">
										忘记密码
								</el-link>
									<el-button class="col" @click="login" type="danger">登录</el-button>
							</div>
							<br>
									<router-link style="font-size: 12px;" to="/register">没有账号，立即注册</router-link>
							</el-form>
					</div>
				</div>
	</div>
</template>

<script>
	import {Ajax} from '../../assets/js/ajax.js'
	export default{
		name:'LoginBody',
		data:function(){
			return{
				loginform:{
					account:'',
					password:''
				}
			}
		},
		methods:{
			login:function(){//登录
				let data ="account="+this.loginform.account
				+"&password="+this.loginform.password
				Ajax("post","user/login",data,(data)=>{
					if(data.success==true){
						this.$router.push('/')	
					}else{
						alert(data.message)
					}
				}) 
			}
		}
	}
</script>

<style>
	.loginPagelogin{
		display: flex;
		justify-content: space-around;
		align-items: center;
		height: 420px;
		background-color: #A0CFFF;
	}
	.zptitle{
		font-size: 11px;background-color: #FDE2E2;height: 30px;
		color:#999999;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.loginform{
		height: 300px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
</style>
